/* eslint-disable prettier/prettier */
<template>
  <div>
    <h2></h2>

    <input type="file" accept=".torrent" hidden multiple @change="parseBtFile" id="bt-file-select">
    <label for="bt-file-select">bt</label>
    <input type="file" hidden id="meta-file-select">
    <label for="meta-file-select">metalink</label>

<div v-if="type == 'bt'"> 
  <br><div> bt,: </div>
  <select multiple v-model="selectedBtFile">
    <option v-for="(file, idx) of btInfo.files" :key="idx" :value="idx"> {{ file.name }} </option>
  </select>
  <button @click="startBtDownload"></button>
</div>
<div v-if="type == 'url'"> </div>
<div v-if="type == 'meta'"> </div>
    <textarea v-model="uris" name="" id="" cols="30" rows="10"></textarea>
    <button @click="start"></button>
  </div>
</template>

<script>
import parseTorrent from 'parse-torrent'
// import events from 'events'
// import utils from 'util'


// bt base64 
function getFileContent(file) {

  return new Promise((resolve,) => {
    var fr = new FileReader()
    fr.onload = function() {
      console.log('frrs',fr.result)
      var commaPos = fr.result.indexOf(',')
      resolve(fr.result.slice(commaPos + 1))
    }
    
    fr.readAsDataURL(file)
  })

}
export default {
  name:'NewTask',
  props:['aria2'],
  data() {
    return {
      uris: '',
 selectedBtFile: [],
      btInfo: null,
      btBase64: null,
      type: 'url'
    }
  },
  methods: {
    async parseBtFile(e) {
      var files = e.target.files[0]
      console.log(files)
      var btFileBase64 = await getFileContent(files)
      console.log(btFileBase64)
      var btFileBuffer = Buffer.from(btFileBase64, 'base64')
      var parsedTorrent = parseTorrent(btFileBuffer)
      console.log(parsedTorrent)


      this.btBase64 = btFileBase64
      this.btInfo = parsedTorrent
      this.type = 'bt'

      // await window.aria2.addTorrent(btFileContent)
      // this.$router.push('/')
    },
    async startBtDownload() {
       await this.aria2.addTorrent(this.btBase64, [], {
         selectFile: this.selectedBtFile
       })
      this.$router.push('/')

    },
    async start() {
      var uris = this.uris.split('\n').map(it => it.trim())
      var taskIds = await this.aria2.addUri(uris)
      console.log('tid',taskIds)
      this.$router.push('/')
    },
  }
}
</script>
